.password_wrap{
    padding: 15px 0;
    .fass_form{
        display: none;
    }
    .password_wrap_inner{
        padding: 10px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
    }
}
.organization_info{
   padding: 30px;
    .single_organization_info{
        display: grid;
        grid-template-columns:90px auto ;
        align-items: center;
        grid-gap: 8px;
        .organization_subheader{
            border-right: 1px solid #cbd4db;
            padding: 4px;
            margin-bottom: 4px;
        }
    }   
}

.primary_checkbox {
	display: inline-block;
	position: relative;
	width: 100%;
	height: 18px;
	margin: 0;
	flex: 100% 0 0 !important;
	line-height: 18px;
}

.upload_your_photo{
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    .thumb_upoad_display{
        height: 50px;
        margin-right: 16px;
        position: relative;
        width: 50px;
        cursor: pointer;
        overflow: hidden;
    }
    .thumb_upoad{
        background: transparent;
        background-image: none;
        border: 1px dashed #9ca6af;
        fill: #9ca6af;
        align-items: center;
        border-radius: 50%;
        box-sizing: border-box;
        display: inline-flex;
        justify-content: center;
        transition-duration: 200ms;
        transition-property: border;
        width: 100%;
        height: 100%;
        background-image: url(../img/avatar.svg);
        &:hover{
            border: 2px solid #7C32FF;
        }
        .upload_file{
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            opacity: 0;
            z-index: 9;
            cursor: pointer;
        }
    }
    .upload_photo_des{
        span{
            position: relative;
            color: #7C32FF;
            font-size: 13px;
            display: inline-block;
            cursor: pointer;
            .upload_file{
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                opacity: 0;
                z-index: 9;
                cursor: pointer;
            }
        }
        p{
            font-size: 11px;
            line-height: 16px;
        }
    }
}

.header_iner .pm_project_top__header .pm_share__project .project_owner_head {
	font-size: 11px;
	height: 24px;
	line-height: 24px;
	min-width: 24px;
	width: 24px;
	color: #fff;
	text-transform: uppercase;
	text-align: center;
	border-radius: 50%;
	display: flex;
	width: 100%;
	background: transparent;
    position: relative;
    .thumb{
        box-shadow: 0 0 0 2px #fff,inset 0 0 0 1px rgba(255,255,255,.1);
        position: relative;
        margin-left: -4px;
    }
}