.modal_tab_container{
    height: 140px;
    overflow: auto;
    padding: 0 32px;
}

.modal_common_tab{
    border-bottom: 1px solid #ECEEF4;
    padding-left: 14px;
    padding: 0 32px !important;
    li{
        a{
            font-size: 12px;
            font-weight: 500;
            color: #415094;
            padding: 0;
            padding:21px 20px;
            text-transform: uppercase;
            position: relative;
            @media #{$small_mobile} {
                padding: 15px 8px;
                font-size: 11px;
            }
            &::before{
                position: absolute;
                content: '';
                width: 100%;
                height: 1px;
                bottom: 0;
                background: linear-gradient(90deg, #7C32FF 0.47%, #C738D8 100%);
                opacity: 0;
                visibility: hidden;
                transition: .3s;
                left: 0;
            }
            &.active::before{
                opacity: 1;
                visibility: visible;
            }
        }
    }
}

