/******** header css here *********/
.header_iner{
    // padding: 27px 30px;
    background-color: transparent ;
    position: fixed;
    // width: 85%;
    top: 0;
    z-index: 99;
    // .... 
    position: relative;
    margin: 0 0 0 0;
    padding: 5px 25px;
    // width: 100%;
    @media #{$small_mobile} {
        justify-content: flex-end !important;
        padding: 5px 15px;
    }
    @media #{$tab} {
        justify-content: flex-end !important;
    }
    @media #{$tab}{
        // width: 100%;
        // padding: 20px;
        margin: 0;
    }
    @media #{$tab_device} {
        // margin: 0 10px;
    }
    @media #{$small_mobile} {
        // margin:0 0 15px 0;
        margin: 0;
    }
    @media #{$large_mobile} {
        // margin:0 0 15px 0;
        margin: 0;
    }
    @media #{$medium_device}{
        // width: 80%;
    }
    // @media #{'only screen and (min-width: 1200px)'} {
    //     margin-bottom: 60px;
    // }
    .page_tittle{
        @media #{$tab}{
            // padding-left: 40px;
        }
        @media #{$small_mobile}{
            // padding-left: 25px;
        }
        h3{
            font-size: 18px;
            font-weight: 500;
            text-transform: capitalize;
            color: #222222;
            margin-bottom: 2px;
            @media #{$tab}{
                margin-right: 15px;
            }
        }
        .page_subtitle{
            font-size: 12px;
            font-weight: 300;
            color: #7e7172;
            margin-bottom: 0;
            .page_subtitle_inenr {
                color: #7e7172;
                &.active_subcat{
                    color:#fe1724;
                    white-space: nowrap;
                }
                i{
                    margin-left: 10px;
                    margin-right: 7px;
                    font-size: 12px;
                }
                svg {
                    position: relative;
                    margin: 0 7px 0 10px;
                    top: -1px;
                    @media #{$small_mobile} {
                        margin: 0 2px 0 2px;
                    }
                }
            }
        }
    }

    .btn_1{
        margin: 0 30px;
    }
    .notification{
        @media #{$tab}{
            margin: 0 10px;
        }
        i{
            color: $text_color_3;
            position: relative;
            font-size: 20px;
            &:after{
                position: absolute;
                content: "";
                right: -1px;
                top: 0;
                height: 9px;
                width: 9px;
                background-color: $base_color;
                border-radius: 50%;
            }
        }
    }
    .serach_field-area {
        position: relative;
        width: 140px;
        .search_inner{
            form{
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }
            input{
                width: 140px;
                height: 32px;
                border-radius: 30px;
                border: 1px solid #9ca6af;
                padding-bottom: 0;
                padding-left: 35px;
                font-weight: 300;
                transition: .01s;
                background: #fff;
                &:focus{
                    width: 511px;
                }
                &::placeholder{
                    font-weight: 300;
                }
            }
            button{
                font-size: 16px;
                padding-left: 11px;
                i{
                    font-size: 14px;
                    color: #6f7782;
                    font-weight: 300;
                }
            }
        }
        form{
            margin-bottom: 0 !important;
        }
        @media #{$mobile_device} {
            display: none;
        }
        @media #{$tab_device} {
           width: auto;
           left: 40px;
        }
        @media #{$tab_device} {
            display: none;
        }
        @media #{$medium_device} {
           width: auto;
           margin-right: 0px;
           display: none;
        }
    }
    .pm_project_top__header{
        display: inline-flex;
        flex: 1 1 auto;
        padding-left: 35px;
        @media #{$medium_device} {
            padding-left: 30px;
        }
        @media #{$tab} {
            // display: none;
            position: absolute;
            top: 43px;
            width: 100%;
        }
        .pm_project__icon{
            display: flex;
            align-items: center;
            margin-right: 16px;
            position: relative;
            button{
                width: 48px;
                height: 48px;
                line-height: 48px;
                text-align: center;
                border-radius: 12px;
                border: 0;
                color: #fff;
                font-size: 25px;
                background: #cbd4db;
                i{
                    line-height: 48px;
                }
            }
            .project_color_dropdown{
                background: #fff;
                border-radius: 4px;
                box-shadow: 0 0 0 1px rgba(111,119,130,.15),0 5px 20px 0 rgba(21,27,38,.08);
                -moz-box-sizing: border-box;
                box-sizing: border-box;
                width: 236px;
                position: absolute;
                top: 100%;
                left: 0;
                z-index: 999;
                opacity: 0;
                visibility: hidden;
                transition: .3s;
                &.color_dropdown_open{
                    opacity: 1;
                    visibility: visible;
                }
                .color_picker_hedear{
                    padding: 24px;
                    .color_chose_List{
                        display: grid;
                        // grid-template-columns: repeat(8,1fr);
                        grid-gap: 3px;
                        grid-template-columns: repeat(auto-fill, minmax(28px, 1fr));
                    }
                }
                .icon_picker_body{
                    border-bottom: 1px solid #e8ecee;
                    border-top: 1px solid #e8ecee;
                    height: 230px;
                    overflow: auto;
                    padding: 8px;
                    .icon_List{
                        display: grid;
                        // grid-template-columns: repeat(8,1fr);
                        grid-gap: 5px;
                        grid-template-columns: repeat(auto-fill, minmax(44px, 1fr));
                    }
                }
                .project_color_footer{
                    padding: 13px;
                    .permission_list{
                        li{
                            margin-bottom: 0;
                        }
                    }
                }
            }
        }   
        .pm_project__title{
            display: flex;
            flex: 1;
            flex-direction: column;
            position: relative;
            margin-top: 5px;
            top: 3px;
            .pm_project_title_top{
                display: flex;
                align-items: center;
                h3{
                    font-size: 20px;
                    font-weight: 400;
                    color: #151b26;
                    white-space: nowrap;
                    margin-bottom: 0;
                    @media #{$medium_device} {
                        font-size: 16px;
                    }
                }
                .square_arrow_down_btn{
                    flex: 32px 0 0;
                    height: 32px;
                }
                .pm_title_details{
                    height: 32px;
                    width: 32px;
                    text-align: center;
                    @media #{$tab} {
                        display: none !important;
                    }
                    i{
                        font-size: 16px;
                        color: #6f7782;
                        line-height: 32px;
                    }
                }
                .project_rating{
                    width: 32px;
                    height: 32px;
                    text-align: center;
                    @media #{$tab} {
                        display: none;
                    }
                    i{
                        font-size: 16px;
                        color: #8da3a6;
                        line-height: 32px;
                    }
                }
                .proejct_status{
                    margin-left: 5px;
                    white-space: nowrap;
                    @media #{$tab} {
                        display: none !important;
                    }
                    .status_dot{
                        width: 6px;
                        height: 6px;
                        flex: 6px 0 0;
                        border-radius: 50%;
                        border: 1px solid #6f7782;
                        margin-right: 6px;
                        position: relative;
                        top: 5px;
                    }
                    span{
                        font-size: 13px;
                        color: #6f7782;
                        font-weight: 300;
                    }
                    
                }
            }
            .pm_tab_navBar{
                ul{
                    display: flex;
                    // position: relative;
                    // top: 3px;
                    li{
                        a{
                            font-size: 12px;
                            font-weight: 300;
                            color: #6f7782;
                            text-transform: capitalize;
                            padding-bottom: 6px;
                            &.active{
                                color: #151b26;
                                box-shadow: inset 0 -2px #151b26;
                            }
                            &:hover{
                                box-shadow: inset 0 -2px #cbd4db;
                                color: #151b26;
                            }
                        }
                        &:not(:last-child){
                            margin-right: 24px;
                            @media #{$medium_device}{
                                margin-right: 8px;
                            }
                        }
                    }
                }
            }
        }
        .pm_share__project{
            display: flex;
            align-items: center;
            position: relative;
            padding-right: 15px;
            margin-right: 15px;
            @media #{$medium_device} {
                // margin-left: 5px;
                // padding-right: 5px;
                margin-right: 0;
            }
            @media #{$tab} {
                display: none;
            }
            &::before{
                border-left: 1px solid #e8ecee;
                content: "";
                display: block;
                height: 32px;
                margin-left: 16px;
                position: absolute;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
            }
            .project_owner_head{
                background-color: #aa62e3;
                font-size: 11px;
                height: 24px;
                line-height: 24px;
                min-width: 24px;
                width: 24px;
                color: #fff;
                text-transform: uppercase;
                text-align: center;
                border-radius: 50%;
            }
            .pm_share_btn{

            }
        }
    }
    .header_right{
        .action_list{
            margin-left: 10px;
            > li{
                line-height: 1;
                margin-right: 8px;
            }
        }
        .header_notification_warp{
            margin-right: 57px;
            margin-left: 40px;
            @media #{$small_mobile} {
                margin-right: 25px;
                margin-left: 20px;
            }
            @media #{$tab_device} {
                margin-right: 35px;
                margin-left: 25px;
            }
            li{
                display: inline-block;
                position: relative;
                @media #{$small_mobile} {
                    position: static;
                }
                @media #{$large_mobile} {
                    position: static;
                }
                @media #{$tab} {
                    position: static;
                }
                &:last-child a{
                    margin-right: 0;
                }
                > a{
                    font-size: 20px;
                    color: $base_color;
                    position: relative;
                    margin-right: 29px;
                    display: inline-block;
                    i{
                        transition: .3s;
                        color: #707DB7;
                    }
                    &.gredient_hover {
                        &:hover{
                            i{
                                @extend %gradient_text;
                            }
                        }
                    }
                    @media #{$small_mobile} {
                        margin-right: 15px;
                        font-size: 15px;

                    }
                   >span.notification_count{
                        position: absolute;
                        width: 20px;
                        height: 20px;
                        line-height: 20px;
                        text-align: center;
                        color: #fff;
                        display: inline-block;
                        @extend %gradient_1;
                        font-size: 10px;
                        border-radius: 50%;
                        right: -8px;
                        top: -4px;


                    }
                }
            }
            li.scroll_notification_list{
                position: relative;

            }
        }
        .select_style {
            @media #{$small_mobile} {
                display: none !important;
            }
            @media #{$large_mobile} {
                display: none !important;
            }
        }
    }
}
.profile_info{
    position: relative;
    // lms none 
    // display: none;
    img{
        max-width: 24px;
        max-height: 24px;
        border-radius: 50%;   
        cursor: pointer;
        @media #{$tab}{
            max-width: 30px;
        }
    }
    .profile_info_iner{
        position: absolute;
        right: 0;
        background-color: $base_color;
        text-align: right;
        width: 215px;
        padding: 30px 30px 20px;
        opacity: 0;
        visibility: hidden;
        top: 65px;
        @include transform_time(.5s);
        @include border-radius(5px);
        &::before{
            position: absolute;
            content: '';
            width: 0;
            height: 0;
            border-style: solid;
            top: -14px;
            right: 10px;
            border-width: 0 15px 20px 15px;
            border-color: transparent transparent $base_color transparent;
        }
        a{
            display: block;
            
        }
        p{
            font-size: 12px;
            font-weight: 300;
        }
        h5{
            font-size: 12px;
            color: $white;
        }
    }
    &:hover{
        .profile_info_iner{
            opacity: 1;
            visibility: visible;
            top: 65px;
        }
    }
    .profile_info_details{
        margin-top: 20px;
        border-top: 1px solid $border_color;
        padding-top: 10px;
        a{
            color: $white;
            font-size: 13px;
            display: block;
            padding: 10px 0;
            &:hover{
                color: $white;
                i{
                    color: $white;
                }
            }
        }
        i{
            color: #cccccc;
            font-size: 12px;
            margin-left: 20px;
            @include transform_time(.5s);
        }
    }
}
.admin .sidebar_icon{
    position: absolute !important;
    top: 50%;
    // left: 30px;
    @include transform(translateY(-50%));
    right: auto;
    left: 20px;
    background: transparent !important;
    @media #{$small_mobile}{
        right: auto;
        // left: 0px;
        left: 70px;
        top: 55%;
    }
    @media #{$large_mobile}{
        right: auto;
        left: 0px;
    }
    @media #{$tab}{
        right: auto;
        left: 0px;
        left: 70px;
        top: 55%;
    }
    i{
        font-size: 20px;
        color: #415094;
        @media #{$small_mobile}{
            font-size: 18px;
        }
    }
}

/******** header css end *********/

  
