.recent_work_viewFile{
    max-width: 840px;
    margin: 0 auto;
    .main-title{
        
        padding-left: 20px;
        padding-top: 20px;
        h3{
            border-bottom: 1px solid rgba(130, 139, 178,.2);
            padding-bottom: 10px;
        }
    }
    .recent_work_viewFile_lists{
        display: flex;
        flex-flow: row wrap;
        padding-top: 16px;
        .Single_recent_work{
            // recent_work_thumb 
            background: transparent;
            padding: 15px;
            border-radius: 30px;
            display: flex;
            align-items: center;
            flex-direction: column;
            height: 220px;
            min-width: 152px;
            padding: 16px 0;
            width: 152px;
            transition: .3s;
            .recent_work_thumb{
                width: 120px;
                height: 120px;
                border-radius: 15px;
                background: #7F32FE;
                .recent_work_top{
                    padding: 5px 10px;
                    opacity: 0;
                    visibility: hidden;
                    transition: .3s;
                    position: relative;
                    top: 3px;
                    .icon{
                        i{
                            color: #fff;
                            font-size: 16px;
                        }
                    }
                    .normal_dropdown_btn .dropdown-toggle{
                        background: transparent ;
                        i{
                            color: #fff;
                        }
                        &:hover{
                            background: #fff;
                            i{
                                color: #151B26;
                            }
                        }
                    }
                }
                .work_icon{
                    text-align: center;
                    position: relative;
                    transform: translateY(0);
                    transition: .3s;
                    i{
                        font-size: 50px;
                        color: #fff;
                        
                    }
                }
                .recent_work_bottom{
                    opacity: 0;
                    visibility: hidden;
                    transition: .3s;
                    position: relative;
                    top: 3px;
                    .recent_work_assignd{
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        position: relative;
                        left: 4px;
                        .single_assignd{
                            margin-left: -4px;
                            width: 24px;
                            height: 24px;
                            border-radius: 50%;
                            img{
                                width: 100%;
                                height: 100%;
                                border-radius: 50%;
                            }
                        }
                    }
                }
            }
            .recent_work_content{
                text-align: center;
                span{
                    font-size: 13px;
                    color: #415094;
                    font-weight: 400;
                    line-height: 13px;
                    display: block;
                    margin: 10px 0 5px 0;
                }
                p{
                    font-size: 11px;
                    color: #6f7782;
                    font-weight: 400;
                    line-height: 1;
                }
            }
            &:hover{
                background: #f6f8f9;
                .recent_work_thumb{
                    .work_icon{
                        transform: translateY(-3px);
                    }
                    .recent_work_top,
                    .recent_work_bottom{
                        opacity: 1;
                        visibility: visible;
                        top: 0px;
                    }
                }
            }
        }
    }
}

