            // }
        }
    }
}

// admin_profile_Wrap 
.admin_profile_Wrap{
    // margin-top: 10px;
    margin-bottom: 30px;
    ul{
        li{
            background: #fff7f8;
            border-top: 1px solid #eee1e2;
            border-bottom: 1px solid #eee1e2;
            padding: 30px 0 30px 30px;
            @media #{$medium_device} {
                padding: 30px 0 30px 20px;
            }
            &.mm-active{
                // padding: 30px 0 27px 30px;
            }
            a{
                display: block;
                .admin_profile_inner{

                    .thumb{
                        img{
                            width: 50px;
                            height: 50px;
                            @include border-radius(50%);
                        }
                    }
                    .welcome_rext{
                        margin-left: 15px;
                        h5{
                            font-size: 14px;
                            font-weight: 500;
                            color: #222222;
                            margin-bottom: 1px;
                            white-space: nowrap;
                            @media #{$medium_device} {
                                font-size: 12px;
                            }
                        }
                        span{
                            display: block;
                            font-size: 12px;
                            color: #7e7172;
                            font-weight: 300;
                            white-space: nowrap;
                            @media #{$medium_device} {
                                font-size: 11px;
                            }
                        }
                    }
                }
            }
            ul{
                padding-left: 70px;
                // padding-top: 30px;
               li{
                   padding: 0;
                   border: 0;
                   a{
                    font-size: 13px;
                    color: #7e7172;
                    font-weight: 400;
                    padding-bottom: 17px;
                   }
                   &:last-child{
                       a{
                           padding-bottom: 0;
                       }
                   }
                   &:first-child{
                       a{
                          margin-top: 29px;
                       }
                   }
               } 
            }
        }
    }
    .metismenu .has-arrow::after{
        top: 38%;
    }
}


