.team_memberList{
    display: flex;
    align-items: center;
    padding-left: 39px;
    height: 20px;
    line-height: 1;
    .small_avater{
        width: 20px;
        height: 20px;
        border-radius: 50%;
        margin-right: 5px;
        position: relative;
        cursor: pointer;
        z-index: 10;
        img{
            width: 100%;
            border-radius: 50%;
        }
        .hover_img{
            position: absolute;
            left: 0;
            top: 0;
            width: 100px;
            height: 100px;
            top: 35px;
            opacity: 0;
            visibility: hidden;
            transition: .3s;
            img{
                width: 100%;
                border-radius: 0;
            }
        }
        &:hover{
            .hover_img{
                opacity: 1;
                visibility: visible;
                top: 25px;
            }
        }
    }
}

#sidebar_menu,#sidebar_menu2{
    &.metismenu .left_arrow_menu.has-arrow{
        padding-left: 45px !important;
        position: relative;
        &::after{
            left: 0;
            right: auto;
            left: 30px;
        }
    }
}
#sidebar_menu.metismenu,#sidebar_menu2.metismenu{
    .create_pro_btns{
        position: absolute;
        top: 0;
        right: 0;
        &.main_sujj_more{
            opacity: 0;
        }
        button{
            border: 0;
            background: transparent ;
            color: #fff;
            width: 42px;
            height: 42px;
            padding: 0;
        }
        .dropdown-menu.pm_dropdown_menu{
            span{
                font-size: 13px;
                font-weight: 400;
                color: #000;
                text-transform: capitalize;
                padding: 5px 15px ;
                display: block;
            }
            .dropdown-item{
                background: #fff !important ;
                color: #000;
                border: 0 !important;
                padding-left: 15px;
                text-transform: capitalize;
                &:hover{
                    background-color: #e8ecee !important;
                }
            }
        }
    }
    .create_pro_btns{
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1000;
        &.main_sujj_more{
            right: 30px !important;
            opacity: 0;
            visibility: hidden;
            transition: .3s;
            transform: scale(0);
        }
        &.sub_sujj_more{
            right: 5px !important;
            opacity: 0;
            visibility: hidden;
            transition: .3s;
            transform: scale(0);
        }
        button{
            border: 0;
            background: transparent ;
            color: #fff;
            width: 42px;
            height: 42px;
            padding: 0;
        }
        .dropdown-menu.pm_dropdown_menu{
            span{
                font-size: 13px;
                font-weight: 400;
                color: #000;
                text-transform: capitalize;
                padding: 5px 15px ;
                display: block;
            }
            .dropdown-item{
                background: #fff !important ;
                color: #000;
                border: 0 !important;
                padding-left: 15px;
                text-transform: capitalize;
                &:hover{
                    background-color: #e8ecee !important;
                }
            }
        }
    }
}

