.board{
    overflow: hidden;
}
.scrollable_board{
    // margin-top: 8px;
    padding: 0 24px;
    background: #f6f8f9;
    padding-top: 8px;
    overflow: hidden;
    overflow-x: auto;
    height: calc(100% - 50px);

    .scrollable_board_list{

    }
}
.scrollable_board_list {
    // display: grid;
    // grid-template-columns: repeat( auto-fill , minmax(304px,1fr));
    // grid-auto-flow: column;
    display: flex;
    flex-direction: row;
    height: 100%;
}
.single_board_list{
    margin-right: 20px;
    height: 100%;
    // overflow-y:auto;
    .add_board{
        background: #fff;
        border-radius: 4px;
        box-shadow: 0 1px 4px 0 rgba(21,27,38,.08);
        align-items: center;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        color: #6f7782;
        cursor: pointer;
        display: inline-flex;
        fill: #6f7782;
        flex-shrink: 0;
        font-size: 13px;
        height: 32px;
        justify-content: center;
        line-height: 32px;
        min-width: 50px;
        padding: 0 8px;
        transition-duration: 200ms;
        transition-property: border,box-shadow,color,fill;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        width: 280px;
    }
}

.board_scroll_container{
    margin-top: 8px;
    .board_item{
        margin-bottom: 8px;
        align-items: center;
        background: #fff;
        border-radius: 4px;
        box-shadow: 0 1px 4px 0 rgba(21,27,38,.08);
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        transition: box-shadow 100ms,transform 100ms,background-color 100ms,border-color 100ms;
        width: 280px;
        cursor: pointer;
        display: block;
        position: relative;
        border: 1px solid transparent;
        padding: 16px;
        
        .board_details_poup {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            z-index: 1;
        }
        &:hover{
            .board_name{
                .normal_dropdown_btn{
                    opacity: 1;
                    visibility: visible;
                }
            }
            .board_body{
                .board_body_left{
                    .unassign_input{
                        opacity: 1;
                        visibility: visible;
                    }
                    .drag_date_box.unassign_date{
                        opacity: 1;
                        visibility: visible;
                        button{
                            opacity: 1;
                        }
                    }
                    .drag_date_box{
                        button{
                            opacity: 1;
                        }
                    }
                }
            }
        }
        .board_name{
            h4{
                font-size: 13px;
                font-weight: 400;
                color: #151b26;
                margin-bottom: 0;
                position: relative;
                z-index: 2;
                cursor: text;
            }
            .normal_dropdown_btn{
                opacity: 0;
                visibility: hidden;
                z-index: 12;
                button{
                    background: transparent !important ;
                }
                
            }
        }
        .board_body{
            .board_body_left{
                .unassign_input{
                    opacity: 0;
                    visibility: hidden;
                }
                .assigned_member {
                    position: relative;
                    z-index: 12;
                }
                .drag_date_box{
                    button{
                        opacity: 0;
                        position: relative;
                        z-index: 12;
                    }
                    &.unassign_date{
                        opacity: 0;
                        visibility: hidden;
                        button{
                            opacity: 1;
                            position: relative;
                            z-index: 12;
                        }
                    }
                }
            }
        }
    }
}

.board_column_header{
    height: 32px;
    width: 280px;
    h4{
        font-size: 16px;
        font-weight: 400;
        color: #273240;
        margin-bottom: 0;
        cursor: pointer;
    }
}

#myTabContent2 {
    height: calc(100% - 72px);
    @media #{$tab} {
        padding-top: 91px;
    }
    @media #{$small_mobile} {
        height: 100%;
    }
}
.gray_content {
    height: 100%;
    background: #f6f8f9;
}