.square_arrow_down_btn {
	height: 24px;
	min-height: 24px;
	min-width: 24px;
	width: 24px;
	display: flex;
	align-items: center;
	justify-content: center;
    font-size: 11px;
    button{
        border: 0;
        border: 0;
        height: 100%;
        width: 100%;
        color: #6f7782;
        background: transparent;
        padding: 0;
    }
    &.show{
        button{
            color: #7C32FF;
        }
    }
}
.multy_dropdown{
    &.multy_dropdown_left{
        ul{
            left:-100%;
            @media #{$small_mobile}{
                left: 0%;
                top: 110%;
            }
        }
        >li:hover {
            ul{
                opacity: 1;
                visibility: visible;
                top: 0;
                @media #{$small_mobile} {
                    left: 0 !important;
                    top: 100%;
                    z-index: 99;
                }
            }
            > a{
                background:#e8ecee;
            }
        }
    }
    li{
        position: relative;
        >a{

        }
        ul{
            position: absolute;
            left: 100%;
            top: 10%;
            width: 100%;
            background: #fff;
            opacity: 0;
            visibility: hidden;
            transition: .3s;
            background: #fff;
            border-radius: 4px;
            box-shadow: 0 0 0 1px rgba(111,119,130,.15),0 5px 20px 0 rgba(21,27,38,.08);
            -moz-box-sizing: border-box;
            box-sizing: border-box;
        }
    }
    >li:hover {
        ul{
            opacity: 1;
            visibility: visible;
            top: 0;
        }
        > a{
            background:#e8ecee;
        }
    }
}


.normal_dropdown_btn{
    &.show{
        .dropdown-toggle{
            color: #7C32FF;
            background: #e8ecee;
        }
    }
   .dropdown-toggle{
        background: #fff;
        font-size: 12px;
        padding: 0;
        padding: 5px;
        padding: 0 8px;
        line-height: 24px;
        border: 0;
        border-radius: 4px;
        color: #6f7782;
        font-weight: 500;
        transition: .3s;
        cursor: pointer;
        min-height: 24px;
        &:hover{
            color: #7C32FF;
            background: #e8ecee;
            color: #273240;
        }
        &:after{
            display: none;
        }
   } 
}

.transparent_dropdown_btn{
    &.show{
        .dropdown-toggle{
            color: #7C32FF;
            background: transparent ;
        }
    }
   .dropdown-toggle{
        background: transparent ;
        font-size: 13px;
        padding: 0;
        padding: 5px;
        padding: 0 8px;
        line-height: 24px;
        border: 0;
        border-radius: 4px;
        color: #6f7782;
        font-weight: 300;
        transition: .3s;
        text-transform: capitalize;
        cursor: pointer;
        min-height: 24px;
        display: flex;
        align-items: center;
        span{
            white-space: nowrap;
            margin-right: 5px;
            line-height: 1;
            border-bottom: 1px dotted transparent ;
        }
        &:hover{
            color: #7C32FF;
            background: transparent ;
            color: #273240;
            span{
                border-bottom: 1px dotted #6f7782;
            }
        }
        &:after{
            display: none;
        }
   } 
}
.minWidth_icon{
    font-size: 12px;
}
.square_btn_32px{
    width: 32px;
    height: 32px;
    line-height: 32px;
    button:hover{
        color:#7C32FF;
    }
}


// page_toolbar_wrapper start

.page_toolbar_wrapper{
    background: #fff;
    padding: 12px;
    // border-top: 1px solid #e8ecee;
    padding-left: 25px;
    @media #{$small_mobile} {
        flex-wrap: wrap;
    }
    .left_toolbar{
        align-items: center;
        display: flex;
        margin-right: auto;
    }
    .right_toolbar{
        @media #{$small_mobile}{
            margin-top: 15px;
        }
        .normal_dropdown_btn{
            margin-left: 5px;
            @media #{$small_mobile}{
                margin-bottom: 5px;
                margin-left: 0;
            }
        }
    }
}

.order_page_topbar{
    display: flex;
    justify-content: space-between;
    background: #fff;
    @media #{$small_mobile} {
        flex-direction: column;
    }
    .order_page_topbar_left{
        display: flex;
        flex: 1 0 auto;
        justify-content: space-between;
        align-items: center;
        padding-left: 25px;
        &:hover{
            background: #f6f8f9;
        }
        .task_headeing{
            span{
                width: 30px;
                display: inline-block;
                margin-left: 4px;
            }
        }

    }
    .order_page_topbar_right{
        .order_list{

            display: flex;
            @media #{$small_mobile} {
                flex-wrap: wrap;
            }
            .single_order_list{
                display: flex;
                align-items: center;
                justify-content: space-between;
                font-size: 12px;
                font-weight: 400;
                border: 1px solid #e8ecee;
                padding: 0 0 0 10px;
                width: 120px;
                cursor: pointer;
                border-right: 0 !important;
                color: #6f7782;
                margin-top: -1px;
                font-weight: 300;
                @media #{$small_mobile} {
                    flex: 1 0 0;
                }
                &:hover{
                    background: #f6f8f9;
                }
            }
        }
    }
}

.mini_width_icon{
