.task_group_wrapper{
    @media #{$small_mobile} {
        width: 100%;
        overflow-x: auto;
        overflow-y: hidden;
    }
    @media #{$large_mobile} {
        width: 100%;
        overflow-x: auto;
        overflow-y: hidden;
    }
}
// task_group_header 
.task_group_header{
    background: #fff;
    padding-left: 25px;
    height: 50px;
    .toggle_button{
        height: 50px;
        cursor: pointer;
        padding: 0 7px;
        i{
            line-height: 50px;
            display: inline-block;
            color: #151b26;
            font-size: 16px;
            &.roted{
                transform: rotate(-90deg);
            }
        }
    }
    .task_group_title{
        cursor: pointer;
        .create_list{
            font-size: 16px;
            color: #415094;
            cursor: pointer;
            opacity: 0;
        }
        .square_arrow_down_btn{
            opacity: 0;
            
        }
        &:hover{
            .square_arrow_down_btn,.create_list{
                opacity: 1;
            }
        }
    }

}

.task_group_list{
    background: #fff;
    @media #{$small_mobile} {
        min-width: 1300px;
    }
    @media #{$large_mobile} {
        min-width: 1300px;
    }
    .task_seperate_single_list{
        height: 28px;
        align-items: center;
        width: 100%;
        &:hover{
            background-color: #f6f8f9;
        }
        .task_number{
            font-size: 12px;
            margin-right: 8px;
            margin-left: 10px;
            min-width: 30px;
        }
        .complete_mark{
            font-size: 15px;
            margin: 0 15px 0 5px;
            cursor: pointer;
            flex: 0 0 auto;
            margin: 0 3px 0 0;
            transition: .3s;
            color: #a3acb8;
            &:hover{
                color: #25E8C8;
                opacity: 1;
            }
        }
        .task_name{
            margin-bottom: 0;
            flex: 0 1 auto;
            min-width: 1px;
            overflow: hidden;
            margin: 0 15px 0 8px;
            line-height: 1;
            .task_textarea{
                border: 0;
                padding: 5px;
                height: auto;
                height: 24px;
                white-space: nowrap;
                font-size: 13px;
                color: #151b26;
                border: 1px solid transparent;
                &::placeholder{
                    color: #151b26;
                    font-size: 13px;
                }
                &:focus,&:hover{
                    background: #fff;
                    border: 1px solid #cbd4db;
                        border-top-color: rgb(203, 212, 219);
                        border-right-color: rgb(203, 212, 219);
                        border-bottom-color: rgb(203, 212, 219);
                        border-left-color: rgb(203, 212, 219);
                    border-radius: 2px;
                    cursor: text;
                }
            }
        }
        .details_show_buton{
            display: flex;
            align-items: center;
            cursor: pointer;
            justify-content: flex-end;
            display: flex;
            flex: 1 0 auto;
            height: 100%;
            -webkit-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
            padding: 0 15px;

            span{
                text-transform: capitalize;
                font-size: 12px;
                font-weight: 400;
                color: #6f7782;
                opacity: 0;
                visibility: hidden;
                transition: .3s;
                display: flex;
                align-items: center;
                position: relative;
                left: -10px;

            }
            i{
                display: block;
                font-size: 11px;
                color: #6f7782;
                margin-left: 5px;
            }
        }
    }
}
.separated_sticky_cell{
    display: flex;
    left: 0;
    flex: 1 1 auto;
    min-width: 1px;
    justify-content: space-between;
    align-items: center;
    &:hover{
        background-color: #f6f8f9;
        .details_show_buton span{
            opacity: 1;
            visibility: visible;
            left: 0;
        }
    }
}
.seperated_min_box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 12px;
    font-weight: 400;
    border: 1px solid #e8ecee;
    padding: 0 0 0 10px;
    width: 120px;
    cursor: pointer;
    height: 28px;
    margin-right: -1px;
}

.cell_border{
    background-color: #fff;
    border: 1px solid #e8ecee;
    border-left-color: rgb(232, 236, 238);
    border-left-style: solid;
    border-left-width: 1px;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    z-index: 0;
    align-items: center;
    display: flex;
    height: 28px;
    overflow: hidden;
    &:hover{
        background-color: #f6f8f9;
        border-color: #cbd4db;
        z-index: 12;
    }
}


.sortitem{background:#fff;padding-left:20px;}
.sortitem .sorthandle{
    position:absolute;
    top:6px;
    bottom:5px;
    left:5px;
    width:12px;
    height: 12px;
    display:none;
    opacity: 0;
    transition: .3s;
    cursor: grab !important;
    background-image:url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAQAAAAECAYAAACp8Z5+AAAAB3RJTUUH3wIDBycZ/Cj09AAAAAlwSFlzAAALEgAACxIB0t1+/AAAAARnQU1BAACxjwv8YQUAAAAWSURBVHjaY2DABhoaGupBGMRmYiAEAKo2BAFbROu9AAAAAElFTkSuQmCC');}
.sortitem .sorthandle{display:block;}

.task_seperate_single_list.sortitem:hover .sorthandle{
    opacity: 1;
}
.task_group_wrapper.sortitem > .sorthandle{
    width: 12px;
    height: 12px;
    top: 18px;
}
.task_group_wrapper.sortitem:hover > .sorthandle{
    opacity: 1;
}
.drag_date_box{
    width: 100%;
    position: relative;
    &:hover{
        button{
            opacity: 1;
        }
    }
    &.visible_date{
        button{
            width: 32px;
            height: 32px;
            line-height: 32px;
            opacity: 1;
            i{
                font-size: 16px;
            }
        }
        input{
            padding-left: 40px;
            color: #6f7782 !important;
            z-index: 1;
            &::placeholder{
                font-size: 13px;
                color: #6f7782 !important;
            }
            &:focus{
                color: #6f7782 !important;
                background: #fff !important;
            }
        }
    }
    button{
        position: absolute !important;
        left: 0;
        right: auto;
        padding: 0;
        margin: 0;
        left: 0;
        width: 24px;
        height: 24px;
        border: 1px dashed #6f7782 ;
        border-radius: 50%;
        text-align: center;
        line-height: 24px;
        top: 50%;
        transform: translateY(-50%);
        left: 3px;
        transition: .3s;
        opacity: 0;
        &:hover,&:focus{
            border: 1px solid #273240 !important;
            i{
                color: #273240 !important;
            }
        }
        i{
            position: relative;
            font-size: 11px;
            top: -1px;
            color: #9ca6af;
        }
    }
    input{
        padding: 0;
        font-size: 11px;
        padding-left: 30px;
        border: 0 !important;
        color: #6f7782 !important;
        z-index: 1;
        &::placeholder{
            font-size: 11px;
            color: #6f7782 !important;
        }
        &:focus{
            color: #6f7782 !important;
            background: #fff !important;
        }
    }
}
