.asign_box_dropdown{
    width: 100%;
    button{
        border: 0;
        background: transparent;
        width: 100%;
        padding: 0;
    }
    .assigned_member{
        position: relative;
        &:hover{
            .remove_assigned{
                opacity: 1;
                visibility: visible;
            }
        }
        span{
            font-size: 11px;
            font-weight: 400;
            color: #273240;
        }
        .remove_assigned{
            position: absolute;
            height: 28px;
            width: 24px;
            background: #f6f8f9;
            text-align:center;
            right: 0;
            top: -1px;
            opacity: 0;
            visibility: hidden;
            transition: .3s;
            cursor: pointer;
            i{
                font-size:12px ;
                color: #6f7782;
                line-height: 28px;
                &:hover{
                    color: #273240;
                }
            }
        }
    }

}
