/* -------------------------------------------------------------------------- */
/*                            project_slide_details                           */
/* -------------------------------------------------------------------------- */
.project_slide_details{
    background-color: #fff;
    border-left: 1px solid #e8ecee;
    border-top: 1px solid #e8ecee;
    bottom: 0;
    box-shadow: 0 5px 20px 0 rgba(21,27,38,.08);
    display: flex;
    flex-direction: column;
    // left: 100%;
    max-width: 700px;
    position: absolute;
    top: 71px;
    transition: .3s ease-in;
    width: 0;
    z-index: 600;
    right: 0;
    opacity: 1;
    width: 700px;
    visibility: visible;
    transform: translateX(100%);
    @media #{$tab} {
        width: 100%;
        top: 0;
    }
    &.project_slide_details_board{
        opacity: 1;
        visibility: visible;
        position: relative ;
        width: 100%;
        transform: translateX(0%);
        max-width: 100%;
        top: 0;
        .project_d_body{
            height: 700px !important;
            overflow: auto;
        }
        .AttachmentImthumb_big_preview{
            max-width: 80%;
            img {
                height: 480px;
                max-width: 100%;
                width: 100%;
                @media #{$tab} {
                    height: auto;
                }
            }
        }
    }
    &.project_slide_details_active{
        opacity: 1;
        visibility: visible;
        transform: translateX(0%);
        // width: 55%;
    }
    .project_slide_details_inner{
        height: 100%;
        .project_slide_details_iner{
            height: 100%;
            overflow: hidden;
        }
        .project_slide_details_header{
            padding: 0 24px;
            height: 48px;
            background: #fff;
            box-shadow: 0 1px 4px 0 rgba(21,27,38,.08);
            @media #{$small_mobile}{
                padding: 0 15px;
            }
            .project_slide_left{
                flex-grow: 1;
                .primary-btn{
                    @media #{$small_mobile} {
                        padding: 7px 13px;
                        font-size: 11px;
                    }
                }
            }
            .square_arrow_down_btn{

            }
        }
        .project_d_body{
            padding: 0 24px;
            // margin-top: 10px;
            padding: 0 24px;
            // margin-top: 10px;
            padding-top: 10px;
            height: calc(100% - 330px);
            overflow: hidden;
            overflow-y: auto;
            @media #{$tab} {
                height: calc(100% - 360px);
            }
            .project_d_name{
                textarea{
                    border: 0;
                    padding: 5px;
                    white-space: nowrap;
                    font-size: 24px;
                    color: #151b26;
                    font-weight: 500;
                    line-height: 32px;
                    border: 1px solid transparent;
                    width: 100%;
                    height: 42px;
                    &::placeholder{
                        color: #151b26;
                        font-size: 13px;
                    }
                    &:focus,&:hover{
                        background: #fff;
                        border: 1px solid #cbd4db;
                            border-top-color: rgb(203, 212, 219);
                            border-right-color: rgb(203, 212, 219);
                            border-bottom-color: rgb(203, 212, 219);
                            border-left-color: rgb(203, 212, 219);
                        border-radius: 2px;
                        cursor: text;
                    }
                }
            }
            .task_d_filed{
                .single_task_d_field{
                    display: flex;
                    min-height: 40px;
                    .task_d_left{
                        flex: 100px 0 0;
                        font-size:12px;
                        font-weight: 300;
                        color: #6f7782;
                        padding-right: 8px;
                        display: flex;
                        align-items: center;
                    }
                    .task_d_right{
                        display: flex;
                        flex: 1 1 auto;
                        min-width: 1px;
                        display: flex;
                        align-items: center;
                    }
                }
            }
        }
    }
}
.assign_d_hover{
    padding: 4px 16px;
    border: 1px solid transparent;
    border-radius: 4px;
    &:hover{
        background-color: #f6f8f9;
        border-color:#6f7782 ;
    }
}
.task_d_project_taken{
    > a{
        font-size: 12px;
        color: #273240;
        padding: 0 10px;
        border-radius: 10px;
        line-height: 20px;
        &:hover{
            background: #e8ecee;
        }
    }
    .task_d_project_dropdown {
        span{
            font-size:12px;
            color: #6f7782;
            font-weight: 400;
            margin: 0 4px;
            line-height: 20px;
        }
    }
    &:hover{
        .project_tkn_btn{
            opacity: 1;
            visibility: visible;
        }
    }
}
.project_tkn_btn{
    opacity: 0;
    visibility: hidden;
    transition: .3s;
    .square_arrow_down_btn{
        button{
            color: #6f7782;
            font-size: 12px;
            cursor: pointer;
            &:hover{
                color:#273240 ;
            }
        }
    }
}

.project_d_thumbs{
    display: flex;
    .attach_img_preview{
        width: 100px;
        height: 64px;
        align-items: center;
        background: #f6f8f9;
        border: 1px solid #e8ecee;
        border-radius: 8px;
        margin-right: 8px;
        margin-bottom: 8px;
        position: relative;
        border-radius: 8px;
        overflow: hidden;
        &:hover::before{
            opacity: .6;
            visibility: visible;
        }
        &::before{
            content: '';
            background: #151b26;
            height: 100%;
            opacity: 0;
            position: absolute;
            width: 100%;
            left: 0;
            top: 0;
            visibility: hidden;
            transition: .3s;
        }
        img{
            width: 100%;
            height: 100%;
        }
    }
    >ul{
        >li{

        }
    }
}
.project_d_noteLists{
    background: #f6f8f9;
    border-bottom: 1px solid transparent;
    flex-shrink: 0;
    margin: 0 -24px;
    margin-top: 0px;
    padding: 8px 24px 0;
}
.doted_sqare_btn{
    align-items: center;
    border: 1px dashed;
    border-radius: 8px;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    color: #9ca6af;
    cursor: pointer;
    display: flex;
    fill: #9ca6af;
    height: 100%;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 64px;
    width: 64px;
    margin-bottom: 8px;
}

.AttachmentImthumb_big_preview {
	align-items: center;
	background: #f6f8f9;
	border: 1px solid #e8ecee;
	border-radius: 8px;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	cursor: pointer;
	display: flex;
	justify-content: center;
	max-width: 100%;
	outline: 0;
	overflow: hidden;
    position: relative;
    &:hover{
        border: 1px solid #9ca6af;
    }
    img{
        height: 480px;
        max-width: 100%;
        @media #{$tab} {
            height: auto;
        }
    }
}

.project_d_footer{
    background: #f6f8f9;
    box-shadow: 0 -1px 3px 0 rgba(21,27,38,.15);
    padding: 8px 24px;
    @media #{$small_mobile} {
        flex-wrap: wrap;
    }
    .project_d_footer_right{
        .project_d_footer_bottom{
            align-items: center;
            background: #f6f8f9;
            border-top: 0;
            display: flex;
            min-height: 40px;
            padding: 4px 32px 5px;
            label{
                align-items: center;
                color: #6f7782;
                display: flex;
                flex-shrink: 0;
                font-size: 11px;
                justify-content: flex-end;
                margin-right: 4px;
                margin-bottom: 0;
            }
            .follower_list{
                border: 1px solid transparent;
                border-radius: 4px;
                display: flex;
                flex: 1 1 auto;
                padding: 4px 8px;
            }
            .leave_task{
                align-items: center;
                display: inline-flex;
                flex-shrink: 0;
                height: 24px;
                white-space: nowrap;
                cursor: pointer;
                &:hover{
                    text-decoration: underline;
                }
            }
        }
    }
}

.dark_gray{
    color: #6f7782 !important;
    &:hover{
        color: #7C32FF !important;
    }
}