/* -------------------------------------------------------------------------- */
/*                                    file wrapper                            */
/* -------------------------------------------------------------------------- */
.project_file_wrapper{
    padding: 32px 20px;
    @media #{'(min-width: 1200px)'} {
        padding: 32px 90px;
    }
    @media #{$min_1680px} {
        padding: 32px 150px;
    }

    .project_file_wrapper_inner{
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(410px, 1fr) );
        grid-gap: 30px;
        @media #{$tab} {
            grid-template-columns: repeat(auto-fill, minmax(250px, 1fr) );
        }
        .single_project_file{
            background: #fff;
            border: 1px solid #cbd4db;
            border-radius: 8px;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            color: inherit;
            overflow: hidden;
            text-align: left;
            max-height: 275px;
            transition: .3s;
            position: relative;
            z-index: 4;
            &:hover{
                background: #f6f8f9;
                border: 1px solid #9ca6af;
                .project_file_preview{
                    border-color:#9ca6af;
                }
            }
            .project_file_d_open{
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                cursor: pointer;
                z-index: 1;
            }
            .project_file_header{
                align-items: center;
                display: flex;
                background: #fff;
                transition: .3s;
                .img_icon{
                    height: 32px;
                    margin: 16px;
                    min-width: 32px;
                    width: 32px;
                    position: relative;
                    z-index: 3;
                    i{
                        font-size: 32px;
                    }
                }
                .project_file_info{
                    display: flex;
                    flex-direction: column;
                    .img_title{
                        font-size: 13px;
                        font-weight: 400;
                        color: #151b26;
                        line-height:20px;
                        position: relative;
                        z-index: 3;
                    }
                    .img_label{
                        line-height: 16px;
                        position: relative;
                        z-index: 3;
                       a{
                        font-size: 11px;
                        font-weight: 400;
                        color: #6f7782;
                        line-height:16px;
                        &:hover{
                            color: #7C32FF;
                        }
                       }
                    }
                }
            }
            .project_file_preview{
                border-top: 1px solid #cbd4db;
                transition: .3s;
                img{
                    width: 100%;
                }
            }
        }
    }
}
.file_project_modal button.Next_prev_btn {
    padding: 0;
    border: 0;
    background: transparent;
    width: 40px;
    height: 40px;
    text-align: center;
    line-height: 40px;
    color: #fff;
    position: fixed;
    left: 30px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 20px;
    opacity: .5;
    cursor: pointer;
    z-index: 11;
}
.file_project_modal button.Next_prev_btn.btn-next {
    left: auto;
    right: 30px;
}
.file_project_modal {
    padding-right: 0 !important;
}
.full_width_modal{
    height: 100%;
    margin: 0;
    width: 100% !important;
    max-width: 100%;
    
    .modal-content {
        border: 0;
        border-radius: 0px;
        height: 100%;
        background: #273240;
        .modal_header{
            display: flex;
            flex: 0 0 auto;
            border-bottom: 1px solid rgba(111,119,130,.4);
            .modal_bar_left{
                display: flex;
                flex: 1 1 auto;
                align-items: center;
                justify-content: space-between;
                margin:0 24px;
                .primary-btn{
                    @media #{$small_mobile} {
                        display: none;
                    }
                }
                .modal_d_title{
                    span{
                        font-size: 13px;
                        font-weight: 400;
                        color: #fff;
                        line-height: 20px;
                    }
                    p{
                        font-size: 13px;
                        color: #6f7782;
                        font-weight: 400;
                        line-height: 20px;
                        margin-bottom: 0;
                    }
                }
            }
            .modal_bar_right{
                width: 60px;
                height: 60px;
                border-left: 1px solid rgba(111,119,130,.4);
                display: flex;
                flex: 0 0 auto;
                button{
                    border: 0;
                    background: transparent;
                    color: #fff;
                    font-size: 15px;
                    width: 100%;
                    line-height: 60px;
                    cursor: pointer;
                    color: rgba(255,255,255,.5);
                    transition: .3s;
                    &:hover{
                        color: #fff;
                    }
                }
            }
        }
        .modal-body{
            background: transparent ;
        }
    }
    .modal-body{
        padding: 0;
        .image_preview{
            align-items: center;
            -moz-box-sizing: border-box;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            height: 100%;
            justify-content: center;
            position: relative;
            width: 100%;
            .modal_close_overly{
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                z-index: 1;
            }
            img{
                position: relative;
                z-index: 4;
                @media #{$small_mobile} {
                    width: 100%;
                }
            }
        }
    }
}

/* -------------------------------------------------------------------------- */
/*                                conversation                                */
/* -------------------------------------------------------------------------- */
.conversion_wrapper{
    padding-top: 32px;
    padding-bottom: 40px;
    display: flex;
    justify-content: center;
    @media #{$small_mobile}{
        padding: 30px 15px;
    }
    @media #{$large_mobile} {
        padding: 30px 15px;
    }
    .conversion_wrapper_inner{
        max-width: 585px;
        @media #{$small_mobile} {
            width: 100%;
        }
        @media #{$large_mobile} {
            width: 100%;
        }
        .conversion_card{
            background: #fff;
            border-radius: 4px;
            box-shadow: 0 1px 4px 0 rgba(21,27,38,.08);
            text-align: center;
            padding: 32px 70px;
            max-width: 585px;
            @media #{$small_mobile} {
                width: 100%;
                padding: 32px 30px;
            }
            @media #{$large_mobile} {
                width: 100%;
            }
            .thumb{
                height: 96px;
                width: 96px;
                margin: 0 auto 20px;
                img{
                    width: 100%;
                }
            }
            h3{
                font-size: 20px;
                color: #273240;
                font-weight: 500;
                margin-bottom: 16px;
                line-height: 28px;
            }
            p{
                font-size: 13px;
                line-height: 20px;
                color: #151b26;
                text-align: center;
                a{
                    color: #7C32FF;
                }
            }
        }
    }
}

/* ------------------------- conversion_sender_wrap ------------------------- */
.conversion_sender_wrap{
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 4px 0 rgba(21,27,38,.08);
    padding: 24px 24px 8px 24px;
    .conversion_sender_input{
        display: flex;
        justify-content: space-between;
        padding: 8px 0px 8px 0px;
        input{
            font-size: 20px;
            font-weight: 400;
            line-height: 28px;
            color: #273240;
            border: 0;
            width: 100%;
            &::placeholder{
                color: #6f7782;
            }
            &:focus{
                outline: none;
            }
        }
    }
    .conversion_descreption_text{
        textarea{
            height: 80px;
            border: 0;
            width: 100%;
            color: #273240;
            font-size: 13px;
        }
    }
    .conversion_sender_footer{
        display: flex;
        justify-content: space-between;
        .conversion_sender_footer_left{
            display: flex;
            align-items: center;
            .square_btn_32px {
                button{
                    i{
                        top: 2px;
                        position: relative;
                    }
                }
            }
            .conversion_tool{
                display: flex;
                align-items: center;
                .single_convertion_tool{
                    width: 26px;
                    height: 32px;
                    font-size: 16px;
                    color: #6f7782;
                    text-align: center;
                    line-height: 32px;
                }
            }
        }
        .conversion_sender_footer_right{
            display: flex;
            align-items: center;
            justify-content: flex-end;
            .primary-btn{
                line-height: 32px;
                padding: 0px 15px;
            }
            p{
                font-size: 11px;
                font-weight: 400;
                color: #6f7782;
                line-height: 16px;
                margin-right: 8px;
            }
        }
    }
}

