.Pm_blank_project_area{
    position: relative;
    z-index: 1;
    &::before{
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        width: 100%;
        background: #101A5F;
        left: calc( (100% - 1532px) / 2 + 688px );
        z-index: -1;
        @media #{$tab} {
            display: none;
        }
    }
    .Pm_blank_project_header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 16px 10px;
        .back_icon{
            width: 28px;
            height: 28px;
            border-radius: 6px;
            text-align: center;
            line-height: 28px;
            i{
                color: #151B26;
            }
        }
        .close_icon{
            width: 28px;
            height: 28px;
            border-radius: 6px;
            text-align: center;
            line-height: 28px;
            i{
                color: #fff;
                @media #{$tab}{
                    color: #151B26;
                }
            }
        }
    }
    .Pm_blank_project_content{
        max-width: 1400px;
        display: grid;
        grid-template-columns: 4fr 8fr;
        grid-gap: 60px;
        margin: auto;
        min-height: calc(100vh - 60px);
        align-items: center;
        padding: 0 20px;
        @media #{$tab} {
            grid-template-columns: auto;
            grid-gap: 30px;
        }
        @media #{$min_1200px} {
            padding: 0 40px;
        }
        .Pm_blank_project_preview{
            margin: 20px 0;
            .tab_imgs{
                border-color: #cbd4db;
                border-radius: 8px;
                border-style: solid;
                border-width: 1px;
                box-shadow: 0 5px 20px 0 rgba(21,27,38,.08);
                max-height: 600px;
                width: 100%;
                background: #fff;
                img{
                    width: 100%;
                }
            }
        }
    }
}

.project_view_layout{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 10px;
    grid-row: 80px;
    @media #{$small_mobile} {
        grid-template-columns: repeat(1,1fr);
    }
    // @media #{$large_mobile} {
    //     grid-template-columns: repeat(1,1fr);
    // }
    li{
        a{
            font-size: 16px;
            font-weight: 400;
            color: #415094;
            border: 1px solid #e8ecee;
            border-radius: 8px;
            background: #fff;
            display: flex;
            align-items: center;
            padding: 21px;
            text-transform: capitalize;
            &.active{
                border-color: #7c32ff;
            }
            i{
                font-size: 36px;
                margin-right: 14px;
            }
        }
    }
}